/****************************************************************************/
/****************************************************************************/
// For documentation site, all marked elements marked as "documentation" a blue color.
// E.g., <h1 documentation>blah</h1>, <p documentation>something</p>
[documentation] {
    color: #2196F3 !important;
}
hr[documentation] {
    // https://codepen.io/ibrahimjabbari/pen/ozinB
    height: 30px; 
    border-style: solid; 
    border-color: #2196F3;
    border-width: 1px 0 0 0; 
    border-radius: 20px; 
    &:before { 
        display: block; 
        content: ""; 
        height: 30px; 
        margin-top: -31px; 
        border-style: solid; 
        border-color: #2196F3; 
        border-width: 0 0 1px 0; 
        border-radius: 20px; 
    }
}

