h1, h2, h3, h4, h5, h6 {

    font-family: $font-family-header;

    small {
        font-family: $font-family-header-secondary;
        margin-left: 1em;
        font-size: 60%;
        letter-spacing: -1px;
        text-transform: none;
        font-family: $font-family-sans-serif;
        font-style: normal;
        font-weight: bolder;
        opacity: 0.7;
    }

    &[secondary] {
        // color: red;
        color: gray;
        font-family: $font-family-header-secondary;
        small {
            font-family: $font-family-header;
            font-size: 80%;
            text-transform: none;
            font-style: normal;
        }
    }

    & + h1[secondary],
    & + h2[secondary],
    & + h3[secondary],
    & + h4[secondary],
    & + h5[secondary],
    & + h6[secondary] {
        // margin-top: -2em;
        margin-top: -3rem;
    }

    // // These are subheads
